*,
*::before,
*::after {
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

@font-face {
  font-family: "Oswald";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local("Oswald"), url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
}
@font-face {
  font-family: "Oswald";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: local("Oswald Meduim"), url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
}
.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.wrapper {
  min-width: 320px;
  padding: 0 20px;
}
@media (min-width: 768px) {
  .wrapper {
    padding: 0 30px;
  }
}
@media (min-width: 1280px) {
  .wrapper {
    max-width: 1280px;
    margin: 0 auto;
  }
}

.button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 11px 20px;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  cursor: pointer;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  color: #ffffff;
  font-weight: 400;
  font-style: normal;
  background-color: #68b738;
}
.button:hover {
  background-color: #5eaa2f;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #5eaa2f;
}
@media (min-width: 768px) {
  .button--index {
    padding: 14px 26px;
    font-size: 20px;
    line-height: 20px;
  }
}
.button--primary, .button--secondary {
  font-size: 16px;
  line-height: 20px;
}
.button--secondary {
  color: #444444;
  background-color: #f2f2f2;
}
.button--secondary:hover {
  background-color: #ebebeb;
}
.button--secondary:active {
  color: rgba(68, 68, 68, 0.3);
  background-color: #ebebeb;
}

.card {
  margin: 0 auto;
  padding: 0;
  padding-left: 20px;
  padding-right: 20px;
  display: grid;
}
@media (max-width: 767px) {
  .card {
    width: 100%;
    grid-template-columns: 1fr 1fr;
    grid-template-areas: "picture info" "link link";
    padding-bottom: 26px;
    border-bottom: solid 1px #ebebeb;
  }
  .card:first-child {
    border-top: solid 1px #ebebeb;
    padding-top: 20px;
  }
  .card--last-card {
    width: 100%;
    display: flex;
    margin: 0 auto;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding-top: 0;
  }
}
@media (min-width: 768px) {
  .card {
    grid-template-areas: "picture" "info" "link";
    justify-content: center;
    padding-left: 36px;
    padding-right: 36px;
    padding-bottom: 39px;
    background-color: #f2f2f2;
    margin-top: 76px;
  }
  .card--last-card {
    background-color: #ffffff;
    border: 2px solid #f2f2f2;
  }
}
@media (min-width: 1280px) {
  .card {
    padding-left: 39px;
    padding-right: 35px;
    margin-bottom: 56px;
  }
}

.card__img-link {
  grid-area: picture;
  position: relative;
  height: 112px;
}
.card__img-link--last-card {
  width: 100%;
  height: 30px;
  margin-bottom: 5px;
}
@media (min-width: 768px) {
  .card__img-link {
    height: 127px;
    margin-bottom: 33px;
  }
  .card__img-link--last-card {
    margin-bottom: 49px;
  }
}

.card__img {
  grid-area: picture;
  position: absolute;
  display: block;
  bottom: 5%;
  left: 23%;
}
.card__img--last-card {
  width: 30px;
  height: 30px;
  left: 50%;
  top: 30%;
  transform: translateX(-50%);
}
@media (min-width: 768px) {
  .card__img {
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
  }
  .card__img--last-card {
    top: 44%;
    width: 60px;
    height: 60px;
  }
}
@media (min-width: 1280px) {
  .card__img {
    bottom: 10px;
  }
  .card__img--last-card {
    top: 40%;
    left: 50%;
  }
}

.card__info {
  grid-area: info;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: center;
}
@media (min-width: 768px) {
  .card__info {
    justify-content: center;
    margin-bottom: 33px;
  }
}
.card__info--last-card {
  align-content: flex-start;
}

.card__title {
  margin: 0;
  max-width: 74px;
  padding-bottom: 10px;
}
.card__title--last-card {
  max-width: 100%;
  margin: 0 auto;
  margin-top: 23px;
}
@media (min-width: 768px) {
  .card__title {
    text-align: center;
    max-width: 134px;
    padding-bottom: 18px;
  }
}

.card__product-link {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  color: #222222;
  font-weight: 400;
  font-style: normal;
  text-transform: uppercase;
  padding: 0;
}
@media (min-width: 768px) {
  .card__product-link {
    font-size: 20px;
    line-height: 24px;
    text-align: center;
  }
  .card__product-link--last-card {
    line-height: 22px;
    padding-bottom: 25px;
  }
}
@media (min-width: 1280px) {
  .card__product-link {
    line-height: 21px;
  }
}

.card__order-link {
  grid-area: link;
  margin-top: 12px;
}
@media (min-width: 768px) {
  .card__order-link {
    margin-top: 0;
  }
}

.card__order-link,
.card__assortment-link {
  width: 100%;
  height: 40px;
}

.card__assortment-link {
  height: 40px;
  margin-top: 20px;
}
@media (min-width: 768px) {
  .card__assortment-link {
    align-self: flex-end;
    margin-top: 0;
  }
}
@media (min-width: 1280px) {
  .card__assortment-link {
    align-self: flex-end;
  }
}

.card__text {
  font-family: "Arial", sans-serif;
  font-size: 12px;
  line-height: 16px;
  color: #444444;
  font-weight: 400;
  font-style: normal;
  margin: 0 auto;
  text-align: center;
}
@media (min-width: 768px) {
  .card__text {
    font-size: 16px;
    line-height: 18px;
    max-width: 167px;
  }
}

.card-extra {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 0;
  padding-top: 13px;
  padding-bottom: 19px;
  border-bottom: 1px solid #cdcdcd;
}
.card-extra:first-child {
  border-top: 1px solid #cdcdcd;
}
@media (min-width: 768px) and (max-width: 1280px) {
  .card-extra {
    padding: 18px 36px 29px 38px;
    border: 1px solid #cdcdcd;
    border-bottom: none;
  }
  .card-extra:last-child {
    border-bottom: 1px solid #cdcdcd;
  }
}
@media (min-width: 1280px) {
  .card-extra {
    grid-template-columns: 1fr 1fr 244px;
    column-gap: 38px;
    padding: 9px 0;
  }
}

.card-extra__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 16px;
  color: #222222;
  font-weight: 400;
  font-style: normal;
  text-transform: uppercase;
  margin: 0;
  display: inline-block;
  align-self: center;
}
@media (min-width: 768px) {
  .card-extra__title {
    font-size: 20px;
    line-height: 20px;
  }
}

.card-extra__order-link {
  width: 100%;
  height: 40px;
  font-size: 16px;
}
@media (min-width: 768px) and (max-width: 1280px) {
  .card-extra__order-link {
    grid-column: 1/-1;
  }
}
@media (min-width: 1280px) {
  .card-extra__order-link {
    width: 244px;
    margin-top: 0;
    align-self: center;
  }
}

.catalog-extra__wrapper {
  padding-top: 25px;
}
@media (min-width: 768px) {
  .catalog-extra__wrapper {
    padding-bottom: 40px;
    padding-top: 0;
  }
}
@media (min-width: 1280px) {
  .catalog-extra__wrapper {
    padding-top: 25px;
    display: grid;
    grid-template-columns: auto 245px;
    column-gap: 80px;
    padding-bottom: 86px;
  }
}

@media (min-width: 768px) {
  .catalog-extra__list {
    grid-column: 1/2;
  }
}

.catalog-extra__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 24px;
  line-height: 24px;
  color: #000000;
  font-weight: 400;
  font-style: normal;
  margin: 0;
  margin-bottom: 37px;
  position: relative;
}
@media (min-width: 768px) {
  .catalog-extra__title {
    font-size: 36px;
    line-height: 36px;
    margin-bottom: 80px;
    padding-left: 30px;
  }
}
@media (min-width: 1280px) {
  .catalog-extra__title {
    grid-column: 1/-1;
    padding-left: 0;
  }
}
.catalog-extra__title--title-line::before, .catalog-extra__title--title-line::after {
  content: "";
  position: absolute;
  top: 14px;
  left: 0;
  z-index: -1;
  display: block;
  width: 100vw;
  height: 3px;
  background-color: #68b738;
}
@media (min-width: 768px) {
  .catalog-extra__title--title-line::before {
    top: 22px;
  }
  .catalog-extra__title--title-line::after {
    top: 22px;
    right: 0;
    left: auto;
  }
}
@media (min-width: 1280px) {
  .catalog-extra__title--title-line::before, .catalog-extra__title--title-line::after {
    top: 21px;
  }
}

.catalog-extra-line {
  padding-right: 22px;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .catalog-extra-line {
    padding-left: 37px;
    padding-right: 36px;
  }
}
@media (min-width: 1280px) {
  .catalog-extra-line {
    padding-left: 40px;
  }
}
@media (min-width: 768px) {
  .catalog-extra-line {
    margin-left: -30px;
  }
}
@media (min-width: 1280px) {
  .catalog-extra-line {
    margin-left: -40px;
  }
}

@media (min-width: 768px) {
  .catalog-main__wrapper {
    padding: 0 30px;
    padding-top: 60px;
    margin-bottom: 67px;
  }
}
@media (min-width: 1280px) {
  .catalog-main__wrapper {
    padding: 0 110px;
    margin-bottom: 0;
  }
}

.catalog-main__header {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 36px;
  line-height: 36px;
  color: #000000;
  font-weight: 400;
  font-style: normal;
  margin: 0;
  padding-left: 20px;
  padding-top: 27px;
  padding-bottom: 37px;
}
@media (min-width: 768px) {
  .catalog-main__header {
    font-size: 60px;
    line-height: 60px;
    padding-left: 30px;
    padding-top: 64px;
  }
}
@media (min-width: 1280px) {
  .catalog-main__header {
    padding-left: 110px;
    padding-top: 74px;
    padding-bottom: 94px;
  }
}

.catalog-main__list {
  display: grid;
  gap: 18px;
}
@media (min-width: 768px) {
  .catalog-main__list {
    grid-template-columns: repeat(auto-fill, minmax(252px, 1fr));
    gap: 62px;
  }
}
@media (min-width: 1280px) {
  .catalog-main__list {
    grid-template-columns: repeat(auto-fill, minmax(245px, 1fr));
    column-gap: 80px;
    row-gap: 20px;
  }
}

.contacts {
  position: relative;
  background-color: #ffffff;
}
.contacts--inner-page {
  background-color: #f2f2f2;
}

.contacts__wrapper {
  padding-top: 28px;
  padding-bottom: 26px;
  text-align: left;
}
@media (min-width: 768px) {
  .contacts__wrapper {
    padding-top: 0;
    padding-bottom: 0;
  }
}
@media (min-width: 1280px) {
  .contacts__wrapper {
    padding-top: 95px;
    padding-bottom: 104px;
  }
}

.contacts__info {
  display: grid;
  grid-template-columns: 1fr 1fr;
}
@media (min-width: 768px) {
  .contacts__info {
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding: 66px 42px 55px 41px;
  }
}
@media (min-width: 1280px) {
  .contacts__info {
    width: 570px;
    column-gap: 87px;
    padding: 62px 78px 59px 76px;
    background-color: #ffffff;
    align-items: center;
    position: relative;
    z-index: 2;
  }
}

.contacts__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  color: #111111;
  font-weight: 400;
  font-style: normal;
  text-transform: uppercase;
  margin: 0;
}
@media (min-width: 768px) {
  .contacts__title {
    font-size: 20px;
    line-height: 25px;
    text-align: left;
  }
}

.contacts__address {
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 20px;
  color: #444444;
  font-weight: 400;
  font-style: normal;
  margin: 0;
}
@media (min-width: 768px) {
  .contacts__address {
    max-width: 159px;
    font-size: 16px;
    text-align: right;
    justify-self: flex-end;
  }
}
@media (min-width: 1280px) {
  .contacts__address {
    text-align: left;
    justify-self: flex-start;
    max-width: none;
  }
}

@media (min-width: 768px) {
  .contacts__address-city {
    display: block;
    margin-top: 19px;
  }
}

.contacts__map {
  display: block;
  width: 100%;
  height: 362px;
}
@media (min-width: 768px) {
  .contacts__map {
    height: 400px;
  }
}
@media (min-width: 1280px) {
  .contacts__map {
    background-size: 100% auto;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    z-index: 1;
  }
}
.contacts__map--nojs {
  background-color: #f2f2f2;
  background-image: url("../img/map.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100% auto;
}
@media (min-resolution: 2dppx) {
  .contacts__map--nojs {
    background-image: url("../img/map@2x.png");
    background-size: 100% auto;
  }
}
@media (min-width: 768px) {
  .contacts__map--nojs {
    height: 400px;
    background-image: url("../img/map-desktop.png");
    background-size: auto 400px;
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .contacts__map--nojs {
    background-image: url("../img/map-desktop@2x.png");
  }
}
@media (min-width: 1280px) {
  .contacts__map--nojs {
    background-size: 100% auto;
  }
}

.contacts__map-iframe {
  display: block;
  width: 100%;
  height: 362px;
  border: none;
}
@media (min-width: 768px) {
  .contacts__map-iframe {
    height: 400px;
  }
}

.developer {
  padding: 20px 0;
}
@media (min-width: 768px) {
  .developer {
    padding: 0;
  }
}

.developer__link {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}
.developer__link:active {
  opacity: 0.3;
}
.developer__link:hover > .developer__logo {
  fill: #68b738;
}

.developer__name {
  font-family: "Arial", sans-serif;
  font-size: 16px;
  line-height: 18px;
  color: #444444;
  font-weight: 400;
  font-style: normal;
}
@media (min-width: 768px) {
  .developer__name {
    padding: 0;
    margin-right: 22px;
  }
}

.developer__logo {
  display: block;
  width: 27px;
  height: 34px;
  color: #444444;
}
.developer__logo:hover {
  color: #68b738;
}
.developer__logo:active {
  color: #68b738;
  opacity: 0.3;
}

.example {
  background-color: #eaeaea;
  position: relative;
}
@media (min-width: 768px) {
  .example {
    background-image: linear-gradient(to top, #f2f2f2 462px, #ffffff 462px);
  }
}
@media (min-width: 1280px) {
  .example {
    background-image: linear-gradient(to bottom, #ffffff 135px, #f2f2f2 135px);
  }
  .example::after {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0;
    left: calc(50% + 322px);
    height: calc(100% - 135px);
    background-color: #eaeaea;
  }
}

.example__wrapper {
  padding-top: 27px;
  padding-bottom: 35px;
}
@media (min-width: 768px) {
  .example__wrapper {
    padding-top: 43px;
    padding-bottom: 26px;
    border-top: 1px solid #e6e6e6;
  }
}
@media (min-width: 1280px) {
  .example__wrapper {
    display: grid;
    grid-template-columns: 436px auto;
    column-gap: 134px;
    padding-top: 0;
    padding-bottom: 57px;
    border: none;
  }
}

.example__info {
  margin-bottom: 42px;
}
@media (min-width: 768px) {
  .example__info {
    display: grid;
    grid-template-columns: auto auto;
    grid-template-areas: "title title" "text text" "list total";
    align-items: start;
    margin-bottom: 59px;
  }
}
@media (min-width: 1280px) {
  .example__info {
    grid-template-areas: "title title" "text text" "list list" "total total";
    margin-bottom: 20px;
  }
}

.example__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 36px;
  line-height: 40px;
  color: #000000;
  font-weight: 400;
  font-style: normal;
  margin: 0;
  margin-bottom: 33px;
}
@media (min-width: 768px) {
  .example__title {
    grid-area: title;
    font-size: 60px;
    line-height: 60px;
    margin-bottom: 62px;
  }
}
@media (min-width: 1280px) {
  .example__title {
    height: 136px;
    margin-bottom: 70px;
  }
}

.example__text {
  margin: 0;
  padding: 0;
  margin-bottom: 25px;
}
@media (min-width: 768px) {
  .example__text {
    grid-area: text;
    padding-right: 10px;
    margin-bottom: 54px;
  }
}
@media (min-width: 1280px) {
  .example__text {
    padding-right: 0;
    margin-bottom: 70px;
  }
}

.example__list {
  margin: 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 10%;
  justify-items: center;
  align-items: start;
  text-align: center;
  margin-bottom: 18px;
}
@media (min-width: 768px) {
  .example__list {
    grid-area: list;
    column-gap: 42px;
    justify-self: start;
  }
}
@media (min-width: 1280px) {
  .example__list {
    margin-bottom: 45px;
  }
}

.example__item {
  width: 100%;
  display: flex;
  flex-direction: column;
  padding: 0;
}
@media (min-width: 768px) {
  .example__item {
    min-width: 162px;
  }
}

.example__item-description {
  font-family: "Arial", sans-serif;
  font-size: 12px;
  line-height: 12px;
  color: #000000;
  font-weight: 400;
  font-style: normal;
  background-color: #eaeaea;
  display: inline-flex;
  align-self: center;
  padding: 0 10px;
  margin-top: -8px;
  margin-left: -2px;
}
@media (min-width: 768px) {
  .example__item-description {
    font-size: 14px;
    line-height: 14px;
    padding: 0 11px;
    margin-top: -8px;
    background-color: #ffffff;
  }
}
@media (min-width: 1280px) {
  .example__item-description {
    background-color: #f2f2f2;
  }
}

.example__item-number {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 24px;
  line-height: 24px;
  color: #000000;
  font-weight: 400;
  font-style: normal;
  text-transform: uppercase;
  padding: 15px;
  border: 1px solid #cdcdcd;
  border-radius: 3px;
}
@media (min-width: 768px) {
  .example__item-number {
    font-size: 30px;
    line-height: 37px;
  }
}

.example__total {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 14px;
  line-height: 20px;
  color: #000000;
  font-weight: 400;
  font-style: normal;
  text-transform: uppercase;
  text-align: center;
}
@media (min-width: 768px) {
  .example__total {
    grid-area: total;
    margin: 0;
    padding-top: 10px;
    font-size: 20px;
    line-height: 25px;
    text-align: right;
  }
}
@media (min-width: 1280px) {
  .example__total {
